    <br><el-button style="margin: 10px 0;" type="success" @click="dealR(sum, member)" round>开始发牌</el-button>
  </div>
</template>

<script>
export default {
  name: "inputMemberR",
  data() {
    return {
      sum: sessionStorage.getItem("currentSum"),
      member: [],
      memberDealed: []
    }
  },
  methods: {
    dealR(sum, member){
      this.memberDealed = JSON.parse(JSON.stringify(member)); // 深拷贝
      for(let i = sum - 1; i > 0; i--){
        let r = Math.floor(Math.random() * i);
        // 让i位置和r位置的队员交换位置
        let t = this.memberDealed[i];
        this.memberDealed[i] = this.memberDealed[r];
        this.memberDealed[r] = t;
      }
      sessionStorage.setItem("member", JSON.stringify(this.member));
      sessionStorage.setItem("memberDealed", JSON.stringify(this.memberDealed));
      this.$router.push("/showResultR");
    }
  }
}
</script>

